import thanks from "../images/thanks.jpg";

const ThanksBox = () => {
  return (
    <div className="thanks-box">
      <img src={thanks} alt="" style={{ width: "100%" }} />
      <div className="title-text">
        <div className="thanks"> .</div>
        <div className="dongjin-ina">- -</div>
      </div>
    </div>
  );
};

export default ThanksBox;
