import { useEffect, useRef, useState } from "react";
import CopyButton from "./CopyButton";
import accImg from "../images/acc_img.jpg";
import openIco from "../icons/open.png";
import RevealOnce from "./RevealOnce";

const ShowAccBttn = ({ text, list }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [maxHeight, setMaxHeight] = useState("0px");
  const contentRef = useRef(null);

  const toggleDrawer = () => {
    setIsOpen((prev) => !prev);
  };

  useEffect(() => {
    if (contentRef.current) {
      if (isOpen) {
        setMaxHeight(`${contentRef.current.scrollHeight}px`);
      } else {
        setMaxHeight("0px");
      }
    }
  }, [isOpen]);

  return (
    <div>
      <button className="acc-bttn" onClick={toggleDrawer}>
        <div>{text}</div>
        <img
          src={openIco}
          alt=""
          style={{
            transform: isOpen ? "rotate(-180deg)" : "rotate(0deg)",
            transition: "transform 0.5s ease",
          }}
        />
      </button>

      <div
        ref={contentRef}
        className="acc-drawer"
        style={{
          maxHeight: maxHeight,
          transition: "max-height 0.5s ease",
          overflow: "hidden",
        }}
      >
        {list.map((info, idx) => (
          <div key={`acc-${idx}`} className="acc-info">
            <div>
              <div className="acc-who">{info[0]}</div>
              <div>
                {info[1]} {info[2]}
              </div>
            </div>
            <CopyButton
              text={`${info[1]} ${info[2]}`}
              almsg=" ."
            />
          </div>
        ))}
      </div>
    </div>
  );
};

const AccBox = () => {
  return (
    <RevealOnce className="acc-box">
      <img src={accImg} alt="" width="100%" />
      <div className="title">  </div>
      <div className="acc-letter">
        <p>   </p>
        <p> .</p>
        <p>   .</p>
      </div>
      <div className="acc-bttns">
        <ShowAccBttn
          text=""
          list={[
            ["", "", "978-045427-02-101"],
            ["", "", "110-048-831781"],
            ["", "", "465125-91-106926"],
          ]}
        />
        <ShowAccBttn
          text=""
          list={[
            ["", "", "110-437-946984"],
            ["", "", "224-12-021215-4"],
            ["", "", "666601-04-118304"],
          ]}
        />
      </div>
    </RevealOnce>
  );
};

export default AccBox;
