import titleStamp from "../icons/title_stamp.png";
import RevealOnce from "./RevealOnce";

const StampBox = () => {
  return (
    <RevealOnce className="stamp-box">
      <img src={titleStamp} alt="-" />
      <div className="overlay-text">
        <p>2025. 5. 24.   11</p>
        <p>  </p>
      </div>
    </RevealOnce>
  );
};

export default StampBox;
