// import CopyButton from "./CopyButton";
import naverMap from "../icons/naver_map.png";
import kakaoMap from "../icons/kakao_map.png";
import tMap from "../icons/t_map.png";
import bus from "../icons/bus.svg";
import car from "../icons/car.png";
import RevealOnce from "./RevealOnce";
import mapBack from "../icons/map_back.png";
import mapSubway from "../icons/map_subway.png";

const LocaMap = () => {
  return (
    <div className="loca-map">
      <img
        src={mapBack}
        alt=""
        style={{
          width: "20rem",
        }}
      />
      <img src={mapSubway} alt="" style={{ width: "4rem" }} />
    </div>
  );
};

const LocaButton = ({ text, icoSrc, link1, link2 }) => {
  return (
    <button
      onClick={() => {
        const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);

        if (isMobile) {
          //   
          window.location.href = link1;

          //     
          setTimeout(() => {
            window.location.href = link2;
          }, 800);
        } else {
          //    
          window.open(link2, "_blank", "noopener,noreferrer");
        }
      }}
    >
      <img src={icoSrc} alt="" />
      {text}
    </button>
  );
};

const LocaBox = () => {
  return (
    <RevealOnce className="loca-box">
      <div className="title"> </div>
      <div className="loca-address">
        <div>   1</div>
        <div>  </div>
        {/* <CopyButton
          text="  "
          almsg="  ."
        /> */}
      </div>
      <LocaMap />
      {/* <img width="90%" src={mapImg} alt="" /> */}
      <div className="bttns">
        <LocaButton
          text=""
          icoSrc={naverMap}
          link1="nmap://search?query=  "
          link2="https://map.naver.com/v5/search/%20%20"
        />
        <LocaButton
          text=""
          icoSrc={kakaoMap}
          link1="kakaonavi://search?q=  "
          link2="https://map.kakao.com/?q=  "
        />
        <LocaButton
          text=""
          icoSrc={tMap}
          link1="tmap://search?name=  "
          link2="https://www.google.com/search?q=++"
        />
      </div>
      <div className="bttn-notice">
             .
      </div>
      <div className="howtoget">
        <div>
          <div className="object">
            <img src={bus} alt="" />
            <div></div>
          </div>
          <div className="description">
            <p> 2  4 -  02  </p>
            <p>    -  5</p>
          </div>
        </div>
        <div>
          <div className="object">
            <img src={car} alt="" />
            <div></div>
          </div>
          <div className="description">
            <p>    .</p>
            <p className="fontRed">
                    
            </p>
            <p className="fontRed">    .</p>
          </div>
        </div>
        <div className="notice">
          <p>     </p>
          <p>    .</p>
        </div>
      </div>
    </RevealOnce>
  );
};

export default LocaBox;
