import { useEffect } from "react";

const FinalBox = () => {
  useEffect(() => {
    //  SDK  
    const script = document.createElement("script");
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    script.async = true;
    script.onload = () => {
      if (window.Kakao && !window.Kakao.isInitialized()) {
        window.Kakao.init("a0968cd4dba7e9802a424a955a5fa705"); //  SDK 
      }
    };
    document.body.appendChild(script);

    // Clean up when component is unmounted
    return () => {
      document.body.removeChild(script);
    };
  }, []);

  const shareKakao = () => {
    if (!window.Kakao) return;

    window.Kakao.Share.sendDefault({
      objectType: "feed",
      content: {
        title: " .",
        description: "25 5 24 11 |   ",
        imageUrl:
          "https://raw.githubusercontent.com/dongjin-ina/dongjin-ina.github.io/main/src/images/thumb_3x4.png",
        imageWidth: 900, // 
        imageHeight: 1200,
        link: {
          mobileWebUrl: "https://dongjin-ina.github.io/",
          webUrl: "https://dongjin-ina.github.io/",
        },
      },
      buttons: [
        {
          title: " ",
          link: {
            mobileWebUrl: "https://dongjin-ina.github.io/",
            webUrl: "https://dongjin-ina.github.io/",
          },
        },
      ],
    });
  };

  return (
    <div>
      <button onClick={shareKakao}>  </button>
    </div>
  );
};

export default FinalBox;
